.home {
    height: 100%;
    width: 100%;
}

.home_hero {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    z-index: -1;
    background-image: url('hero_mobile_temp.png');
    background-size: cover;
    background-repeat: no-repeat;
}

.home_booking {
    width: fit-content;
    margin: 15vw auto;
}

.btn.home_bookingBtn {
    box-shadow: 4px 4px 5px #696969;
    border-radius: 3rem;
    height: 4rem;
}

.home_reviews {
    display: none;
}

.home_reviews .card{
    margin: 15%;
}

.home_review_card .card-header {
    font-size: 1.25rem;
}

.home_review_card .home_review_detail {
    font-size: 1rem;
}

@media (orientation: landscape) {
    .home_hero {
        background-image: url('hero_1000w.jpg');
    }

    .home_reviews {
        display: block;
    }

    .home_booking {
        margin: 5em auto 0;
    }
}