.nav {
    margin: 2em;
}

.nav h2 {
    margin: auto;
    font-size: 1.75rem;
    text-align: center;
}

.menu_toggle.dropdown-toggle::after{
    display: none;
}

.nav_options {
    display: none;
}

.nav_options .col {
    text-align: center;
}

@media (orientation: landscape) {
    #menu {
        display: none;
    }

    .nav_options {
        display: block;
    }
}
